// src/api/communityService/comment.ts
import { COMMUNITYSERVICE_API } from "@/config/env";
import { CommunityPostType, CommentResponse, CreateCommentRequest } from "@/types/communityService/communityType";

export async function createComment(
    type: CommunityPostType,
    communityId: number,
    request: CreateCommentRequest,
    userSignId: string
): Promise<number> {
  const url = `${COMMUNITYSERVICE_API}/comments/${type.toLowerCase()}/${communityId}`;

  const response = await fetch(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      "userSignId": userSignId,
    },
    body: JSON.stringify(request),
  });

  if (response.status === 201) {
    return await response.json();
  } else {
    const errorText = await response.text();
    throw new Error(errorText || `ÎåìÍ∏Ä ÏûëÏÑ± Ïã§Ìå®: HTTP ${response.status}`);
  }
}

