// src/pages/community.tsx
import React, { useState, useEffect } from 'react';
import Link from 'next/link';
import Layout from '@/component/layout/MainLayout';
import styles from '@/styles/community/CommunityMain.module.css';
import { getConcernList, getProjectList, getStudyList, Page } from '@/api/communityService/community';
import { getLikeCount, toggleLike, checkLike } from '@/api/communityService/like';
import { getCommentCount } from '@/api/communityService/comment';
import { getTags } from '@/api/communityService/tag';
import { CommunityPostType,
  CreateConcernRequest,
  CreateProjectRequest,
  CreateStudyRequest,
  PostDetailResponse,
  PostSummaryResponse
 } from '@/types/communityService/communityType';
import { useInView } from 'react-intersection-observer';
type TabType = 'all' | 'concern' | 'project' | 'study';

interface ExtendedPostSummary extends PostSummaryResponse {
  postType: CommunityPostType;   // ‚Üê Ï∂îÍ∞Ä!
  likeCount: number;
  commentCount: number;
  isLiked: boolean;
  tags: string[];
}

export default function Community() {
  const [activeTab, setActiveTab] = useState<TabType>('all');
  const [posts, setPosts] = useState<ExtendedPostSummary[]>([]);
  const [page, setPage] = useState(0);
  const [hasMore, setHasMore] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [userSignId, setUserSignId] = useState<string>('');
  const tabs = [
    { id: 'all' as TabType, label: 'Ï†ÑÏ≤¥', emoji: 'All' },
    { id: 'concern' as TabType, label: 'Í≥†ÎØºÏûàÏñ¥Ïöî', emoji: 'Thought' },
    { id: 'project' as TabType, label: 'ÌîÑÎ°úÏ†ùÌä∏', emoji: 'Rocket' },
    { id: 'study' as TabType, label: 'Ïä§ÌÑ∞Îîî', emoji: 'Book' },
  ];

  const loadPosts = async (tab: TabType, pageNum: number): Promise<ExtendedPostSummary[]> => {
    setIsLoading(true);
    try {
      let response: Page<PostSummaryResponse>;
      let postType: CommunityPostType;

      switch (tab) {
        case 'concern':
          response = await getConcernList(pageNum, 10);
          postType = CommunityPostType.CONCERN;
          break;
        case 'project':
          response = await getProjectList(pageNum, 10);
          postType = CommunityPostType.PROJECT;
          break;
        case 'study':
          response = await getStudyList(pageNum, 10);
          postType = CommunityPostType.STUDY;
          break;
        default:
          return [];
      }

      const extendedPosts = await Promise.all(
          response.content.map(async (post) => {
            const [likeCount, commentCount, isLiked, tags] = await Promise.all([
              getLikeCount(postType, post.communityId),
              getCommentCount(postType, post.communityId),
              userSignId ? checkLike(postType, post.communityId, userSignId) : false,
              getTags(post.communityId)
            ]);

            return {
              ...post,
              postType,        // ‚Üê Ïó¨Í∏∞ÏÑú Î™ÖÏãúÏ†ÅÏúºÎ°ú ÎÑ£Ïñ¥Ï§å!
              likeCount,
              commentCount,
              isLiked,
              tags
            };
          })
      );

      return extendedPosts;
    } catch (error) {
      console.error('Í≤åÏãúÍ∏Ä Î°úÎî© Ïã§Ìå®:', error);
      return [];
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    setPosts([]);
    setPage(0);
    setHasMore(true);
    setUserSignId(localStorage.getItem('userSignId') || '');
  }, [activeTab]);

  useEffect(() => {
    if (activeTab !== 'all' && hasMore && !isLoading) {
      loadPosts(activeTab, page).then(newPosts => {
        if (newPosts.length === 0) {
          setHasMore(false);
        } else {
          setPosts(prev => [...prev, ...newPosts]);
          setPage(prev => prev + 1);
        }
      });
    }
  }, [activeTab, page, hasMore, isLoading]);

  // Ï†ÑÏ≤¥ ÌÉ≠ Î°úÏßÅ
  useEffect(() => {
    if (activeTab === 'all' && hasMore && !isLoading) {
      setIsLoading(true);
      Promise.all([
        loadPosts('concern', page),
        loadPosts('project', page),
        loadPosts('study', page)
      ]).then(([concerns, projects, studies]) => {
        const all = [
          ...concerns.map(p => ({ ...p, postType: CommunityPostType.CONCERN } as ExtendedPostSummary)),
          ...projects.map(p => ({ ...p, postType: CommunityPostType.PROJECT } as ExtendedPostSummary)),
          ...studies.map(p => ({ ...p, postType: CommunityPostType.STUDY } as ExtendedPostSummary))
        ].sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime());

        if (all.length === 0) {
          setHasMore(false);
        } else {
          setPosts(prev => [...prev, ...all]);
          setPage(prev => prev + 1);
        }
        setIsLoading(false);
      });
    }
  }, [activeTab, page, hasMore, isLoading]);

  useEffect(() => {
    if (inView && hasMore && !isLoading) {
      setPage(prev => prev + 1);
    }
  }, [inView, hasMore, isLoading]);

  const handleLikeToggle = async (post: ExtendedPostSummary) => {
    if (!userSignId) {
      alert('Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.');
      return;
    }
    try {
      const newIsLiked = await toggleLike(post.postType, post.communityId, userSignId);
      setPosts(prevPosts => prevPosts.map(p =>
          p.communityId === post.communityId
              ? { ...p, isLiked: newIsLiked, likeCount: p.likeCount + (newIsLiked ? 1 : -1) }
              : p
      ));
    } catch (error) {
      alert('Ï¢ãÏïÑÏöî Ï≤òÎ¶¨Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
    }
  };

  const formatTime = (dateString: string) => {
    const date = new Date(dateString);
    const now = new Date();
    const diff = now.getTime() - date.getTime();
    if (diff < 3600000) return `${Math.floor(diff / 60000)}Î∂Ñ Ï†Ñ`;
    if (diff < 86400000) return `${Math.floor(diff / 3600000)}ÏãúÍ∞Ñ Ï†Ñ`;
    return date.toLocaleDateString('ko-KR');
  };

  const getBadgeStyle = (type: CommunityPostType) => {
    switch (type) {
      case CommunityPostType.CONCERN: return styles.badgeConcern;
      case CommunityPostType.PROJECT: return styles.badgeProject;
      case CommunityPostType.STUDY: return styles.badgeStudy;
      default: return styles.badgeConcern;
    }
  };

  return (
      <Layout>
        <div className={styles.container}>
          <header className={styles.header}>
            <h1 className={styles.title}>Ïª§ÎÆ§ÎãàÌã∞</h1>
            <p className={styles.subtitle}>Í∞úÎ∞úÏûêÎì§Ïùò ÏßÑÏßú Ïù¥ÏïºÍ∏∞, Ïó¨Í∏∞ÏÑú ÏãúÏûëÎê©ÎãàÎã§</p>
          </header>

          <div className={styles.tabContainer}>
            {tabs.map(tab => (
                <button
                    key={tab.id}
                    onClick={() => setActiveTab(tab.id)}
                    className={`${styles.tab} ${activeTab === tab.id ? styles.activeTab : ''}`}
                >
                  <span className={styles.tabEmoji}>{tab.emoji}</span>
                  {tab.label}
                </button>
            ))}
          </div>

          <div className={styles.grid}>
            {posts.map(post => (
                <Link
                    key={post.communityId}
                    href={`/community/${post.postType.toLowerCase()}/${post.communityId}`}
                    className={styles.cardLink}
                >
                  <article className={styles.card}>
                    <div className={`${styles.badge} ${getBadgeStyle(post.postType)}`}>
                      {post.postType === CommunityPostType.CONCERN ? 'Í≥†ÎØºÏûàÏñ¥Ïöî' :
                          post.postType === CommunityPostType.PROJECT ? 'ÌîÑÎ°úÏ†ùÌä∏' : 'Ïä§ÌÑ∞Îîî'}
                    </div>
                    <h3 className={styles.cardTitle}>{post.title}</h3>
                    <p className={styles.cardExcerpt}>
                      {post.content.length > 100 ? post.content.substring(0, 100) + '...' : post.content}
                    </p>
                    <div className={styles.tags}>
                      {post.tags.map(tag => (
                          <span key={tag} className={styles.tag}>#{tag}</span>
                      ))}
                    </div>
                    <div className={styles.cardFooter}>
                      <div className={styles.authorInfo}>
                        <span className={styles.author}>{post.authorNickname}</span>
                        <span className={styles.time}>{formatTime(post.createdAt)}</span>
                      </div>
                      <div className={styles.stats}>
                        <button
                            onClick={(e) => {
                              e.preventDefault();
                              handleLikeToggle(post);
                            }}
                            className={`${styles.likeButton} ${post.isLiked ? styles.liked : ''}`}
                        >
                          Ï¢ãÏïÑÏöî {post.likeCount}
                        </button>
                        <span>ÎåìÍ∏Ä {post.commentCount}</span>
                      </div>
                    </div>
                  </article>
                </Link>
            ))}
          </div>

          <div ref={ref} className={styles.loadMore}>
            {isLoading && <p>Î°úÎî© Ï§ë...</p>}
          </div>

          <Link href="/community/write" className={styles.fab}>
            + Í∏ÄÏì∞Í∏∞
          </Link>
        </div>
      </Layout>
  );
}